.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  padding: 0;
  z-index: 20;
}

.header__inner {
  margin: 0 auto;
  padding: 0 30px;
  max-width: 1806px;
}

.header__inner:after {
  content: " ";
  display: table;
  clear: both;
}

.header_big .header__inner {
  padding: 51px 41px 0;
}

.header_wide .header__inner {
  max-width: 1904px;
}

.header_max .header__inner {
  max-width: 1920px;
}

.header__logo {
  float: left;
  margin-top: 30px;
}

.header_big .header__logo {
  margin-top: 0;
}

.header__btn-menu {
  float: right;
}

.header__right {
  float: right;
  margin-top: 57px;
  transform: translateX(calc(100% - 68px));
}

.header_big .header__right {
  margin-top: 0;
  transform: translateX(calc(100% - 92px));
}

.is-loaded .header__right {
  transition: transform .3s ease-in-out;
}

.is-open-menu .header__right {
  transform: translateX(0);
}

.logo {
  width: 90px;
  vertical-align: top;
  display: inline-block;
}

.logo img {
  max-width: 100%;
  display: block;
}

.btn-menu {
  position: relative;
  z-index: 1;
  margin-top: 57px;
  width: 47px;
  height: 37px;
  background-color: transparent;
}

.btn-menu__line {
  position: absolute;
  left: 0;
  height: 8px;
  width: 100%;
  transition: transform .3s;
  background-color: #FF9D11;
}

.btn-menu__line:first-child {
  top: 0;
  transform-origin: left top;
}

.is-open-menu .btn-menu__line:first-child {
  transform: translate(11px, -1px) rotate(45deg);
}

.btn-menu__line:nth-child(2) {
  top: 50%;
  margin-top: -4px;
}

.is-open-menu .btn-menu__line:nth-child(2) {
  transform: scaleX(0);
}

.btn-menu__line:last-child {
  bottom: 0;
  transform-origin: left bottom;
}

.is-open-menu .btn-menu__line:last-child {
  transform: translate(11px, 0) rotate(-45deg);
}

.header_white .btn-menu__line {
  background-color: #fff;
}

.header_big .btn-menu {
  margin-top: 0;
