.slider-recipe__prev, .slider-recipe__next {
  position: absolute;
  top: 96px;
  cursor: pointer;
  background-color: transparent;
}

.slider-recipe__prev .icon, .slider-recipe__next .icon {
  font-size: 68px;
  color: #676a6c;
}

.slider-recipe__prev {
  left: -40px;
}

.slider-recipe__next {
  right: -40px;
}

.footer {
  padding: 100px 0 67px;
  color: #0d70b7;
  font-size: 20px;
  background-color: #ffe1b7;
}

.footer__copy {
  float: left;
}

.footer__soc {
  float: right;
  padding-right: 30px;
}

.footer_small {
  padding: 25px 88px 22px 54px;
}

.footer_white {
  background-color: #fff;
}

.footer_absolute {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  padding: 45px 87px 45px 27px;
  background-color: transparent;
}

.soc {
  font-size: 0;
  line-height: 50px;
}

.soc__item {
  vertical-align: middle;
  display: inline-block;
}

.soc__item:not(:first-child) {
  margin-left: 85px;
}

.soc__link {
  font-size: 34px;
  line-height: 1;
  color: #0d70b7;
  display: block;
}

.soc__link .icon-facebook {
  font-size: 42px;
}

.soc__link .icon-instagram {
  font-size: 37px;
}
